/* common */
* {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
}

li {
    list-style-type: none;
}

#wrap {
    background-color: #423F3E;
    width: 100%;
    margin: 0 auto;
    font-family: 'Nanum Gothic', sans-serif;
}

/*header*/
header {
    display: flex;
    width: 100%;
    height: 100px;
    justify-content: center;
    background-color: #171010;
    .Head {
        padding: 5px 0;
        a{
            text-decoration: none;
            color: white;
            h1{
                font-family: 'Mochiy Pop P One', sans-serif;
            }
        }
        a:hover {
            color: wheat;
        }
    }
}

/*Button 스타일*/
button,
button::after {
    transition: all 1.0s;
}

button {
    background: none;
    width: 100px;
    padding: 10px;
    border: 3px solid #fff;
    border-radius: 5px;
    color: #fff;
    display: block;
    position: relative;
    text-transform: uppercase;
    z-index: 1;
    margin: 0 auto;
    margin-top: 10px;
}

button::before,
button::after {
    background: #fff;
    content: '';
    position: absolute;
    z-index: -1;
}

button:hover {
    color: #000;
}

.btn-style {
    overflow: hidden;
}

.btn-style::after {
    /*background-color: #f00;*/
    height: 100%;
    left: -50%;
    top: 0;
    transform: skew(50deg);
    transition-duration: 0.6s;
    transform-origin: top left;
    width: 0;
}

.btn-style:hover:after {
    height: 120%;
    width: 150%;
}


/* 메뉴 부분 스타일 */

nav {
    width: 100%;
    position: relative;
    .Menu {
        position: absolute;
        list-style-type: none;
        font-weight: bold;
        /*글자 굵기 */
        width: 100%;
        display: flex;
        right: 0;
        flex-flow: row nowrap;
        float: right;
        li {
            border-radius: 1px solid #000;
            padding: 20px 0;
            a{
                display: inline-block;
                text-decoration: none;
                color: white;
                padding: 10px 20px;
                margin: 0 auto;
            }
            a:hover{
                color: wheat;
            }
        }
    }

}



.SideMenu {
    position: fixed;
    border: 5px solid #000;
    border-radius: 30px;
    background: white;
    list-style-type: none;
    font-weight: bold;
    /*글자 굵기 */
    right: 10px;
    top: 200px;
    display: flex;
    flex-flow: column nowrap;
    z-index: 99;
    opacity: 0;
    li {
        border-radius: 1px solid #000;
        a{
            display: inline-block;
            text-decoration: none;
            padding: 10px 20px;
            margin: 0 auto;
            .food_icon {
                margin-left: 10px;
            }
            .nature_icon {
                margin-left: 8px;
            }
        }
        a:hover {
            color: #f4f0e9;
        }
    }
}

/* 왼쪽 내용 부분 스타일 food */
.sub1 {
    display: flex;
    flex-flow: row nowrap;
    background-color: #423F3E;
    height: 1800px;
}

.gallary_section {
    display: flex;
    flex-flow: row wrap;
    width: 100%;
}

.gallary_section2 {
    width: 100%;
}

.box {
    border: 1px solid black;
    box-sizing: border-box;
    padding: 10px;
    margin: 3% 1.25% 3% 1.25%;
    width: 30%;
    height: 30%;
    /* background: url(); */
}

.gallary_list {
    list-style-type: none;
    img{
        margin: 10px;
        float: left;
        position: static;
        width: 50%;
    }
}

.about_area {
    h2 {
        padding: 55px 0 105px;
        text-align: center;
        font-size: 40px;
        color: wheat;
    }
    .place_list {
        display: flex;
        flex-wrap: wrap;
        li {
            float: left;
            width: 30%;
            margin-left: 2%;
            margin-bottom: 2%;
            text-align: center;
            position: relative;
            top: -50px;
            .img_topplace {
                width: 100%;
            }
            h3 {
                font-weight: bold;
                font-size: 18px;
                padding-top: 25px;
                color: wheat;
            }
            p.txt {
                box-sizing: border-box;
                padding: 10px 30px 0;
                width: 100%;
                overflow: hidden;
                word-break: keep-all;
                line-height: 1.8;
                color: rgb(190, 172, 137);
            }
            .view {
                display: inline-block;
                margin-top: 20px;
                padding-top: 5px;
                border-top: 2px solid #333;
                color: rgb(190, 172, 137);
            }
        }
    }
}

/* popup_base */

.popup_base {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    z-index: 200;
    display: none;
    .pop_content {
        position: relative;
        width: 580px;
        margin: 200px auto 0;
        box-sizing: border-box;
        background: #f4f0e9;
        .btn_xpop {
            position: absolute;
            right: 0;
            top: -55px;
            a{
                display: block;
                width: 45px;
                height: 45px;
                text-align: center;
                text-indent: -9999px;
                overflow: hidden;
                background: url(../image/btn_xpop.png) no-repeat;
                background-size: 45px;
            }
        }
        .pop_list li {
            padding: 36px;
            .tit_pop {
                padding: 10px 0 24px;
                font-weight: bold;
                font-size: 30px;
                letter-spacing: -2px;
            }
            .pop_img {
                width: 100%;
            }
            .pop_txt {
                padding-top: 15px;
                display: flex;
                p {
                    padding-top: 10px;
                    line-height: 1.8;
                    font-size: 17px;
                    width: 95%;
                    margin: 0 auto;
                    overflow: hidden;
                    word-break: keep-all;
                    letter-spacing: -2px;
                }
                .Info1 {
                    width: 30%;
                }
                .Info2 {
                    width: 70%;
                    position: relative;
                    span {
                        display: inline-block;
                        position: absolute;
                        right: 0;
                    }
                }
            }
        }
    }
}

/* adventure*/

.sub2 {
    display: flex;
    width: 100%;
    flex-flow: row nowrap;
    background-color: #2b2928;
    height: 800px;
}

.gallary_section2 h2 {
    padding: 55px 0 105px;
    text-align: center;
    font-size: 40px;
    color: wheat;
}

.program_list{
    display: flex;
    flex-wrap: wrap;
    li {
        float: left;
        width: 30%;
        margin-left: 2%;
        margin-bottom: 2%;
        text-align: center;
        position: relative;
        top: -50px;
        .img_place {
            display: block;
            width: 100%;
            height: 300px;
        }
        h3 {
            padding: 40px 0 0;
            font-weight: bold;
            font-size: 22px;
            color: wheat;
        }
        .subttl {
            padding: 25px 0 0;
            font-weight: bold;
            font-size: 16px;
            color: rgb(190, 172, 137);
            letter-spacing: 0;
        }
        .program_content {
            padding: 15px 0 0;
            line-height: 1.8;
            font-size: 15px;
            p {
                width: 100%;
                overflow: hidden;
                word-break: keep-all;
                color: rgb(190, 172, 137);
                .subtxt {
                    display: none;
                }
            }
        }
        .btn_more {
            text-align: center;
            font-size: 12px;
            cursor: pointer;
        }
    }
    li:nth-child(3n+1) {
        clear: both;
    }
}

